import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef, ComponentFactoryResolver, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { routerTransition } from '../../../router.animations';
import { QuickaccessComponent, AllReportService, FileExportService, ItemMasterService, CurrentUserService } from '../../../shared';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { NumberFormat } from 'xlsx/types';

@Component({
  selector: 'app-stock-ledger-report',
  templateUrl: './stock-ledger-report.component.html',
  styleUrls: ['./stock-ledger-report.component.scss']
})
export class StockLedgerReportComponent implements OnInit {

  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('sComboBox') sComboBox: jqxComboBoxComponent;

  storeLedgerReportForm: FormGroup;

  ItemAdapter: any;
  ItemFocus: boolean = false;
  private stockId = 0;
  storeAdapter: any;
  store_data: any;
  userData: any = {};
  private apiUrl: string;
  showGenerateReportButton: boolean = false;
  reportData: any = [];
  itemName: string;
  dateFrom: any;
  dateTo: any;
  openingData: any;
  openingQuantity: number = 0;
  openingTotal: number = 0;
  dataNotFound: boolean = false;

  constructor(
    private fb: FormBuilder,
    private rs: AllReportService,
    private fes: FileExportService,
    private ims: ItemMasterService,
    private cus: CurrentUserService,
    @Inject('API_URL') apiUrl: string,
    private componentFactoryResolver: ComponentFactoryResolver,
  ) {
    this.createForm();
    this.apiUrl = apiUrl;
    this.userData['token'] = this.cus.getTokenData()['token'];
  }

  ngOnInit() {
    this.createQuickAccess();
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' },
        ],
        data: this.userData,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = [];
        let dt = {};
        dt['id'] = '';
        dt['name'] = 'Select All';
        tData.push(dt);
        for (let i = 0; i < data.data.length; i++) {
          tData.push(data.data[i]);
        }
        console.info(tData);
        return tData;
      }
    });
  }
  ngAfterViewInit() {
    this.sComboBox.addItem('Select All');
  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }
  createForm() {
    this.storeLedgerReportForm = this.fb.group({
      'date_from': [null, Validators.required],
      'date_to': [null, Validators.required],
      'item_id': [null, Validators.required],
      'item_name': [null, Validators.required]
    });
  }

  /**
  * inputItemFilter Event is called when Item input field has
  * keyup action followed by 'Enter'
  * Generate Suggestion based on input value entered
  * @param searchString 
  * @param index 
  */
  ItemFilter(searchPr) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode1').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 1) {
      if (temp) {
        this.ItemFocus = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            console.log(response);
            this.ItemAdapter = response;
            console.log(this.ItemAdapter);
          },
          error => {
            console.log(error);
          }
        );
      } else {
        this.ItemFocus = false;
      }
    }
  }

  /**
   * Event fired when option is selected from Item Suggestion Select field
   * Hide Select field after Item Selected.
   * @param selectedEvent 
   * @param index 
   */
  ItemListSelected(selectedEvent) {
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      let displayText = selectedEvent.target.selectedOptions[0].text;
      this.storeLedgerReportForm.get('item_id').patchValue(selectedEvent.target.value);
      this.storeLedgerReportForm.get('item_name').patchValue(displayText);
      this.ItemFocus = false;
    }
  }

  saveBtn(post, generate: boolean) {
    //post = [];
    if (!generate) {
      this.reportData = [];
    }
    if (!generate) {
      this.showGenerateReportButton = true;
    }
    post['limit'] = 'all';
    post['report'] = true;
    post['stock_ledger'] = true;
    if (this.stockId) {
      post['store_id'] = this.stockId;
    }

    if (generate) {
      post['stock_ledger_generate'] = true;
    }

    this.jqxLoader.open();
    this.rs.getStoreLedgerReport(post, generate ? true : false).subscribe((result: any) => {
      if (generate) {
        this.fes.SaveFile(result, 'Stock Ledger Report');
      } else {
        this.dataNotFound = result.actual_report.length ? false : true;
        if (this.dataNotFound) this.jqxLoader.close();
        this.dateFrom = result.date_from;
        this.dateTo = result.date_to;
        this.itemName = result.item_name[0].item_name;
        this.openingData = result.opening;

        this.openingData.forEach(result => {
          this.openingQuantity += (result.quantity || 0);
          this.openingTotal += (parseFloat(result.total) || 0);
        })

        let qty: number = this.openingQuantity;
        let tot: number = this.openingTotal;

        //console.log(isNaN(tot));
        
        result.actual_report.forEach(stockResult => {
          let data = {
            date: stockResult.date,
            type: stockResult.type,
            ref: stockResult.ref,
            remarks: stockResult.remarks,
            inquantity: stockResult.quantity,
            inrate: stockResult.total / stockResult.quantity,
            intotal: stockResult.total,
            bquantity: stockResult.quantity + qty,
            brate: (parseFloat(stockResult.total) + tot) / (stockResult.quantity + qty),
            btotal: parseFloat(stockResult.total) + tot
          }

          qty = stockResult.quantity + qty;
          tot = parseFloat(stockResult.total) + tot;
          this.reportData.push(data);
          this.jqxLoader.close();
        })
      }
      this.jqxLoader.close();
    },
      error => {
        this.jqxLoader.close();
      });
  }

  getStockId($event) {
    this.stockId = $event.args.item.originalItem.id;
  }

}



